<template>
    <div>
        <div class="left-menu-tab new_tab nosrcoll">
            <div class="col-md-12 p-0 ">
                <div class="theme-primary partition">
                    <span class="p-name text-white">Report Listing</span>
                </div>
            </div>
            <loader :is-visible="isLoading"></loader>
            <div class="pt-2 mb-1 w-100 d-none d-lg-block">
                <div class="form-group">
                    <input v-bind:type="'text'" class="form-control search" v-on:keydown="ListFiltering"
                        v-on:keyup="ListFiltering" :placeholder="'Search By Report Name'" id="SearhField" />
                </div>
            </div>
            <PerfectScrollbar>
                <div class="mt-2 mCustomScrollbar _mCS_4 mCS_no_scrollbar " id="divscrollsetuptabs"
                    style="max-height:calc(100vh - 2px)">
                    <ul class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix" id="upper"
                        role="tablist">
                        <li style="display: list-item;"
                            v-for="(item, itemIndex) in searchColumnfilter.length == 0 && !SearchNoRecord ? ReportData : searchColumnfilter"
                            :key="itemIndex">
                            <a class="nav-link left-menu text-truncate" style="width: 80%;" v-on:click="GetReportList(item)"
                                :title="item.REPORTNAME + ' (' + item.FOLDER_NAME + ')'">
                                <i class="fa fa-file-text-o mr-2" aria-hidden="true"></i>{{ item.REPORTNAME + " (" +
                                    item.FOLDER_NAME + ")" }}
                            </a>
                        </li>
                    </ul>
                    <div v-if="SearchNoRecord"
                        class="text-center text-danger d-flex align-items-center justify-content-center norecs pt-2"> {{
                            $t("NoRecordfound") }}
                    </div>
                </div>
            </PerfectScrollbar>
        </div>
        <div>
            <ViewCustomReport v-if="IsReportListing" :key="componentKey" :ReportListParameters="ReportListParameters">
            </ViewCustomReport>
        </div>
    </div>
</template>
<script>
import ViewCustomReport from './ViewCustomReport.vue';
import ReportDataService from "@/services/DataService";
export default {
    components: {
        ViewCustomReport
    },
    data() {
        return {
            isLoading: false,
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            SortBy: '',
            SortExp: '',
            searchcondition: '',
            ReportData: [],
            ReportListParameters: {},
            IsReportListing: false,
            GetDeafultReportData: [],
            Viewcount: 0,
            componentKey: 0,
            searchColumnfilter: [],
            SearchNoRecord: false
        }
    },
    async created() {
        var vm = this;
        await vm.FetchData();
        if (vm.GetDeafultReportData != [] && vm.GetDeafultReportData != undefined) {
            vm.GetReportList(vm.GetDeafultReportData);
            vm.Viewcount += 1;
        }
    },
    methods: {
        async FetchData() {
            var vm = this;
            const params = `searchCondition=${vm.searchcondition}&is_paged=${vm.isPaged}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}`
            await ReportDataService.CustomReportListing(params)
                .then(function (response) {
                    if (response.data != "" && response.data != null) {
                        if (response.data.data.length > 0) {
                            vm.ReportData = response.data.data;
                            vm.GetDeafultReportData = vm.ReportData[0];
                            vm.noRecord = false;
                        } else {
                            vm.noRecord = true;
                            vm.ReportData = [];
                        }
                    }
                    else {
                        vm.noRecord = true;
                        vm.ReportData = [];
                    }
                    setTimeout(function () {
                        vm.ResponsiveDataTable('tablelistingdata')
                    }, 500);
                    setTimeout(function () {
                        vm.SetScrolbar('dark-thick', '', 'customScrollForLeftBar')
                    }, 1000);
                    vm.isLoading = false;
                });
        },
        GetReportList(data) {
            var vm = this;
            vm.isLoading = true;
            vm.IsReportListing = true;
            if (vm.Viewcount > 0) {
                vm.componentKey += 1;
            }
            vm.ReportListParameters = {
                ReportID: data.REPORT_ID,
                ACCESS: data.ACCESS
            }
            vm.isLoading = false;
        },
        ListFiltering: function () {
            var vm = this;
            var input = document.getElementById('SearhField');
            var filterinput = input.value.toUpperCase();
            if (filterinput != null && filterinput != "") {
                $(".multi-collapse").addClass("show");
            }
            else {
                $(".multi-collapse").removeClass("show");
            }
            var searchcol = [];
            var searchfilter = [];
            vm.ReportData.forEach((indexitem) => {
                searchfilter.push(indexitem);
                searchcol = searchfilter.filter(searchitem => {
                    var displayMainColname = searchitem.REPORTNAME + ' (' + searchitem.FOLDER_NAME + ')';
                    return displayMainColname.replace(/\W/g, "").toLowerCase().includes(filterinput.replace(/\W/g, "").toLowerCase());
                })
            })
            vm.searchColumnfilter = searchcol;
            searchfilter = [];
            searchcol = [];
            let count = 0;
            if (vm.searchColumnfilter.length > 0) {
                count++;
            }
            if (count > 0) {
                vm.SearchNoRecord = false
            } else { vm.SearchNoRecord = true; }
            return vm.searchColumnfilter;
        }
    }
}
</script>
<style scoped>
.ps {
    height: 650px;
}
</style>